import React, { Component } from 'react';
import { Container } from 'react-bootstrap';


export default class NavbarLogo extends Component {
    render() {
        return (
            <nav className="navbar">
                <Container>
                    <div className="logo-text">
                        <a href="#!" className="logo">
                            Daniel
                    </a>
    
                        <p>
                            <a href="#!">
                                .inc
                        </a>
                        </p>
                    </div>
                </Container>
            </nav>
        )

    }
} 

