import React, { Component } from 'react';

import NavbarWrapper from './NavbarWrapper.js';
import NavbarLogo from './NavbarLogo.js';
import SocialMediaList from '../SocialMediaList/SocialMediaList.js'; 

import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import Home from '../../Pages/_Home/_Home.js';
import AboutMe from '../../Pages/AboutMe/AboutMe.js';
import WhatIDo from '../../Pages/WhatIDo/WhatIDo.js';
import Works from '../../Pages/Works/Works.js';
import Contacts from '../../Pages/Contacts/Contacts.js';

export default class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {
            btn_toggler_classname: false,
            navbar_wrapper_classname: false,
            social_media_list_classname: false
        }

        this.handleClick = this.handleClick.bind(this);
    }


    handleClick() {
        this.setState({
            btn_toggler_classname: !this.state.btn_toggler_classname,
            navbar_wrapper_classname: !this.state.navbar_wrapper_classname,
            social_media_list_classname: !this.state.social_media_list_classname
          });

         this.state.btn_toggler_classname ? document.body.style.overflowY = 'unset' : document.body.style.overflowY = 'hidden';
    }

    render() {
        return (
            <>
                <header className="header">
                    <NavbarWrapper 
                    navbar_wrapper_classname = {this.state.navbar_wrapper_classname ? "navbar-wrapper active" : "navbar-wrapper"}
                    btn_toggler_classname = {this.state.btn_toggler_classname ? "btn btn-toggler hamburger hamburger--squeeze is-active active" : "btn btn-toggler hamburger hamburger--squeeze"}
                    btn_toggler_onclick = {this.handleClick}
                    />
                    <NavbarLogo />
                    <SocialMediaList 
                    social_media_list_classname = {this.state.social_media_list_classname ? "social-media-box active" : "social-media-box"}
                    />
                </header>

                <Router>
                    <Switch>
                        <Route exact path="/" component={Home}/>
                        <Route exact path="/aboutme" component={AboutMe}/>
                        <Route exact path="/whatido" component={WhatIDo}/>
                        <Route exact path="/works" component={Works}/>
                        <Route exact path="/contacts" component={Contacts}/>
                    </Switch>
                </Router>
            </>
        )
    }
}
