import React, { Component } from 'react';

import HomeHeaderContent from './HomeHeaderContent.js';
import bgVid from '../../../../media/vid/header_bg.mp4';

export default class HomeHeaderSection extends Component {
    render() {
        return (
        <section className="home-header">
            <HomeHeaderContent/>
            <video autoPlay muted loop src={bgVid} type="video/mp4"/>
        </section>
        );
    }
}
    
