import React, { Component } from 'react';
import { Container } from 'react-bootstrap';

import AboutMeCard from './AboutMeCard.js';

export default class HomeAboutMeSection extends Component {

    render() {

        return (

            <section className="about-me">
                <div className="objects">
                    <img className="object_rocket" alt="" src="http://salehriaz.com/404Page/img/rocket.svg" />
                    <div className="earth-moon">
                        <img className="object_earth" alt="" src="http://salehriaz.com/404Page/img/earth.svg" />
                        <img className="object_moon" alt="" src="http://salehriaz.com/404Page/img/moon.svg" />
                    </div>
                    <div className="box_astronaut">
                        <img className="object_astronaut" alt="" src="http://salehriaz.com/404Page/img/astronaut.svg" />
                    </div>
                </div>
                <Container>

                    <h2 className="about-me-title text-shadow">About Me</h2>
                    <p className="about-me-subtitle text-shadow">
                        Here is some information about me
                    </p>
                    <AboutMeCard />
                </Container>
            </section >

        )
    }
}
